<template>
	<view class="box">
		<view class="" v-if="show">
			<view class="phone">
				<view class="text">
					填写土地证信息
				</view>
				<view class="text-right">
					<text>03</text>
					<text>/</text>
					<text>04</text>
				</view>
			</view>
			<view class="">
				<input class="inputs" type="text" v-model="farmName" placeholder="请输入农场名称">
			</view>
			<view class="">
				<picker @change="bindPickerChange($event,seedlingList)" range-key="SeedlingsName" :range="seedlingList">
					<input class="inputs" disabled="true" v-model="seedlingName" type="text" placeholder="请选择需要种植地种苗">
				</picker>
			</view>
			<view class="btn" @click="onNext">下一步</view>
		</view>
		<view class="goumai" v-if="goumaiShow">
			<image src="@/static/img/success@3x.png"></image>
			<view class="text">购买成功</view>
			<view class="land-certificate">
				<view class="user-land">
					<image :src="landData.Image" mode=""></image>
					<view class="">{{landData.NickName||'张三丰'}}</view>
				</view>
				<view class="land-address">
					<view class="">土地位置：</view>
					<view class="">{{landData.Block || 'xx村/农场'}}</view>
				</view>
				<view class="land-address">
					<view class="">土地编号：</view>
					<view class="">{{landData.LandCode || '123456789012345'}}</view>
				</view>
			</view>
			<view class="btn" @click="show=true,goumaiShow=false">
				<text>填写土地证信息</text>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		GetSeedlingListAjax
	} from '@/apis/api/modules/buyland'
	export default {
		name: 'StepThree',
		data() {
			return {
				show: false,
				goumaiShow: true,
				seedlingList: null,
				seedlingName: null,
				seedlingId: null,
				farmName: null
			}
		},
		props: {
			landData: {
				default: {}
			}
		},
		created() {
			this.getData()
		},
		methods: {
			async getData() {
				const result = await GetSeedlingListAjax({
					token: true
				})
				this.seedlingList = result.Result
			},
			bindPickerChange(e, data) {
				this.seedlingId = data[e.detail.value].SeedlingsID
				this.seedlingName = data[e.detail.value].SeedlingsName
			},
			onNext() {
				if (!this.farmName) {
					uni.showToast({
						title: '请输入农场名称',
						icon: 'none'
					});
					return false
				}
				if (!this.seedlingName) {
					uni.showToast({
						title: '请选择地种苗',
						icon: 'none'
					});
					return false
				}
				this.$emit("onNext", this.farmName, this.seedlingId)
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		padding: 40upx 30upx 0;
		box-sizing: border-box;
		overflow: hidden;
	}

	.phone {
		font-size: 40upx;
		font-weight: 500;
		color: #333333;
		line-height: 56upx;
		display: flex;
		justify-content: space-between;

		.text-right {
			text:nth-of-type(1) {
				width: 42upx;
				height: 56upx;
				font-size: 40upx;
				font-weight: 500;
				color: #00D08F;
				line-height: 56upx;
			}
		}
	}

	.inputs {
		width: 100%;
		border-bottom: 2upx solid #efefef;
		font-size: 32upx;
		font-weight: 400;
		line-height: 44upx;
		padding: 40upx 0 30upx;
	}

	.btn {
		margin-top: 60upx;
		width: 690upx;
		height: 104upx;
		background: #00D08F;
		border-radius: 52upx;
		backdrop-filter: blur(20upx);
		font-size: 32upx;
		font-weight: 500;
		color: #FFFFFF;
		line-height: 104upx;
		text-align: center;
	}

	.goumai {
		overflow: hidden;
		text-align: center;

		image {
			padding-top: 60rpx;
			width: 290rpx;
			height: 290rpx;
		}

		.text {
			padding: 30rpx 0 60rpx;
			font-size: 32rpx;
			font-weight: 500;
			color: #00D08F;
			line-height: 44rpx;
		}

		.land-certificate {
			width: 570rpx;
			height: 414rpx;
			background: url('@/static/img/土地证@3x.png') no-repeat;
			background-size: 100% 100%;
			margin: 20rpx 60rpx 80rpx;

			.user-land {
				padding: 120rpx 0 14rpx;

				image {
					padding-top: 0;
					width: 88rpx;
					height: 88rpx;
					border-radius: 50%;
				}
			}
		}

		.land-address {
			display: flex;
			justify-content: space-between;
			font-size: 28rpx;
			font-weight: 400;
			color: #666666;
			line-height: 40rpx;
			padding: 10rpx 30rpx 30rpx;

			&:last-child {
				padding-top: 0;
				padding-bottom: 0;
			}
		}

		.btn {
			width: 370rpx;
			height: 104rpx;
			margin: 0 168rpx;
			line-height: 104rpx;
			background: #00D08F;
			border-radius: 52rpx;
			backdrop-filter: blur(20rpx);
			font-size: 32rpx;
			font-weight: 500;
			color: #FFFFFF;
		}
	}
</style>
